@import url(https://use.fontawesome.com/releases/v5.5.0/css/all.css);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
*
{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
/* Start Styling ToDoApp */
.container
{
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%,-50%);
          transform: translate(-50%,-50%);
  border-radius: 5px;
  width: 600px;
  background-color: #E4E4E4;
}
.container .items
{
  width: 100%;
  margin-bottom: 15px;
}
.container .items p
{
  text-align: center;
  font-size: 20px;
  margin: 10px 0;
}
.container .items header
{
  position: relative;
}
.container .items label
{
  display: inline-block;
  padding: 10px;
  font-size: 20px;
}
.container .items header label
{
  color: #fff;
  background-color: #222;
}
.container .items .lblname,
.container .items .lblage
{
  width: 35%;
  text-align: center;
  border-right: 1px solid #fff;
  border-bottom: 1px solid #fff;
}
.container .items .lblaction
{
  width: 30%;
  text-align: center;
  border-bottom: 1px solid #fff;
  cursor: pointer;
}
.container .items .lblaction:hover
{
  color: red;
}
.container .items div:last-of-type label
{
  border-bottom: none;
}
.container .items .item .lblaction
{
  border-right: 1px solid #fff;
}
.container .items .item .lblname
{
  border-left: 1px solid #fff;
}
/*Start Styling Form*/

.container .form > form
{
  display: flex;
  align-items: center;
  justify-content: space-around;
}
.container .form > form input[type="text"],
.container .form > form input[type="number"]
{
  font-size: 15px;
  width: 35%;
  padding: 7px;
  border: 1px solid #ddd;
}
.container .form > form input[type="submit"]
{
  font-size: 15px;
  width: 30%;
  padding:7px;
  background-color: #4caf50;
  color: #fff;
  border: 1px solid #4caf50;
  cursor: pointer;
}
.container .form > form input[type="text"]:focus,
.container .form > form input[type="number"]:focus,
.container .form > form input[type="submit"]:focus
{
  outline: none;
}


